	module.exports = {"data":{"markdownRemark":{"html":"<h2>All Factors of a Number</h2>\n<p>You can think of factors as the numbers that you multiply to get another number.</p>\n<img src=\"https://upload.wikimedia.org/wikipedia/commons/e/e8/Factor_Tree_of_42.png\" width=\"90\">\n<p>In this case 2, 3, 6, and 7 are all numbers you can multiply to get 42. So, they are all factors of 42. However, they are not all the factors of 42. To find all the factors we can use the algorithm or step by step by process below.</p>\n<p>Start with 1. </p>\n<p>1 * 42 = 42</p>\n<table>\n<thead>\n<tr>\n<th>1</th>\n<th>…</th>\n<th>42</th>\n</tr>\n</thead>\n<tbody>\n</tbody>\n</table>\n<p>Try 2. </p>\n<p>2 * 21 = 42</p>\n<table>\n<thead>\n<tr>\n<th>1</th>\n<th>2</th>\n<th>…</th>\n<th>21</th>\n<th>42</th>\n</tr>\n</thead>\n<tbody>\n</tbody>\n</table>\n<p>Try 3. </p>\n<p>3 * 14 = 42</p>\n<table>\n<thead>\n<tr>\n<th>1</th>\n<th>2</th>\n<th>3</th>\n<th>…</th>\n<th>14</th>\n<th>21</th>\n<th>42</th>\n</tr>\n</thead>\n<tbody>\n</tbody>\n</table>\n<p>Try 4.</p>\n<p>No whole number can be multiplied by 4 to get 42, so we skip it.</p>\n<p>Try 5.</p>\n<p>No whole number can be multiplied by 5 to get 42, so we skip it.</p>\n<p>Try 6.</p>\n<p>6 * 7 = 42</p>\n<table>\n<thead>\n<tr>\n<th>1</th>\n<th>2</th>\n<th>3</th>\n<th>6</th>\n<th>…</th>\n<th>7</th>\n<th>14</th>\n<th>21</th>\n<th>42</th>\n</tr>\n</thead>\n<tbody>\n</tbody>\n</table>\n<p>Since there are no whole numbers between 6 and 7, all of the positive factors have been found. All of the numbers above can have their sign flipped and will continue to be factors of 42. In conclusion, all the factors of 42 are below.</p>\n<table>\n<thead>\n<tr>\n<th>1</th>\n<th>2</th>\n<th>3</th>\n<th>6</th>\n<th>7</th>\n<th>14</th>\n<th>21</th>\n<th>42</th>\n<th>-1</th>\n<th>-2</th>\n<th>-3</th>\n<th>-6</th>\n<th>-7</th>\n<th>-14</th>\n<th>-21</th>\n<th>-42</th>\n</tr>\n</thead>\n<tbody>\n</tbody>\n</table>","fields":{"slug":"/mathematics/all-factors-of-a-number/"},"frontmatter":{"title":"All Factors of a Number"}}},"pathContext":{"slug":"/mathematics/all-factors-of-a-number/"}}

